import Vue from 'vue'
import MessageVue from '@/components/Message/Message'
import Vuetify from 'vuetify/lib'

const MessageConstructor = Vue.extend(MessageVue) // VueVue.extend

let nId = 1

const Message = (options) => {
  const id = 'message-' + nId++
  options = options || {}
  if (typeof options === 'string') {
    options = {
      message: options
    }
  }
  const MessageInstance = new MessageConstructor({
    data: options
  })
  MessageInstance.$vuetify = new Vuetify().framework
  MessageInstance.id = id
  MessageInstance.vm = MessageInstance.$mount() // domVue
  MessageInstance.vm.visible = true
  MessageInstance.dom = MessageInstance.vm.$el
  document.getElementById('app').appendChild(MessageInstance.dom) // dombody
  MessageInstance.dom.style.zIndex = id // Messagez-index
  return MessageInstance.vm
}

['success', 'warning', 'info', 'error'].forEach(type => {
  Message[type] = options => {
    if (typeof options === 'string') {
      options = {
        message: options
      }
    }
    options.type = type
    return Message(options)
  }
})

export default Message
